    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    width: 100vw;
    height: 100vh;
    margin: 0;
}

.header {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    grid-column: 1;
    grid-row: 1;
    font-size: 35px;
    align-items: center;
    gap: 1rem;
    background-color: #f5f5f4;
    font-family: 'Times New Roman', Times, serif;
}

.header img {
    width: 2rem;
    height: auto;
}

.main {
    width: 100%;
    height: 90%;
    background-color: #f5f5f4;
    display: grid;
    grid-template-columns: minmax(400px, 25%) auto;
    grid-template-rows: minmax(50px, 10%) auto;
    gap: 1rem;
    height: 100%;
    width: 100%;
    align-items: center;
    padding: 0.5%;
    font-family: 'Times New Roman', Times, serif;
}

.sidebar {
    background-color: #a8a29e;
    overflow: scroll;
    display: flex;
    flex-direction: column;
    grid-column: 1;
    grid-row: 2;
    height: 100%;
    width: 100%;
    padding: 1rem;
    align-items: center;
    border: 5px solid black;
    font-family: 'Times New Roman', Times, serif;
}

.sidebar-header {
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.sidebar-header button {
    border: none;
    padding: 0.5rem;
    transition: 0.1s;
}

.sidebar-header button:hover{
    filter: invert(1);
    transition: 0.1s;
}

.sidebar-header button:active{
    filter: invert(1);
    scale: 0.9;
    transition: 0.1s;
}

.project-list {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    gap: 1rem;
    margin-top: 2rem;
}

.project {
    cursor: pointer;
    font-size: 25px;
    align-self: center;
}

.project-entry {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 1rem;
    display: none;
    box-shadow: 0 0 50px 20px rgba(255, 255, 255, 0.534);
}

#project-form {
    width: 300px;
    height: 200px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 1rem;
    padding: 2rem;
    font-family: 'Roboto', Helvetica, Arial, sans-serif;
}

form input {
    height: 2.5rem;
    padding: 0.25rem;
}

form button {
    border: none;
    padding: 0.25rem;
    transition: 0.1s;
}

form button:hover{
    filter: invert(1);
    transition: 0.1s;
}

form button:active{
    filter: invert(1);
    scale: 0.9;
    transition: 0.1s;
}

.input {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 0.5rem;
    text-align: center;
}

.formbuttons {
    display: flex;
    justify-content: space-around;
}

.project-area {
    grid-column: 2;
    grid-row: 1 / -1;
    width: 98%;
    height: 95%;
    background-color: #d6d3d1;
    display: flex;
    flex-direction: column;
    padding: 2rem;
    align-items: center;
    justify-content: flex-start;
    gap: 1rem;
    overflow: scroll;
    border: 5px solid black;
}

.buttonarea{
    gap: 0.5rem;
}

.new-todo{
    border: none;
    padding: 0.25rem;
    transition: 0.1s;
}

.new-todo:hover{
    filter: invert(1);
    transition: 0.1s;
}

.new-todo button:active{
    filter: invert(1);
    scale: 0.9;
    transition: 0.1s;
}

.todo-entry {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 1rem;
    display: none;
    box-shadow: 0 0 50px 20px rgba(255, 255, 255, 0.534);
}

#todo-form {
    width: 300px;
    height: 500px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    gap: 1rem;
    padding: 2rem;
    font-family: 'Roboto', Helvetica, Arial, sans-serif;
}

.todo-list {
    padding: 1rem;
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.label {
    font-weight: bold;
    font-size: 0.8rem;
}

.todo-item {
    list-style-type: none;
    display: flex;
    gap: 2rem;
    padding: 1.5rem;
    border: 2px solid black;
    border-radius: 8px;
    background-color: #e5e5e5;
    align-self: flex-end;
}

.todo-item button {
    height: 1.5rem;
    border: 2px solid black;
    padding: .5rem;
    text-align: center;
    display: flex;
    align-items: center;
    border-radius: .25rem;
    transition: 0.1s;
}

.todo-item button:hover{
    filter: invert(1);
    transition: 0.1s;
}

.todo-title {
    font-weight: bold;
    display: flex;
    align-items: center;
    cursor: default;
}

.todo-date, .todo-priority {
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow: hidden;
    cursor: default;
}

#nightmode-btn {
    border: none;
    background-color: transparent;
    transition: 0.2s;
    width: 30px;
    height: auto;
}

#nightmode-btn:hover {
    scale: 1.1;
    transition: 0.2s;
    cursor: pointer;
}

#mute-btn {
    border: none;
    background-color: transparent;
    transition: 0.2s;
    width: 30px;
    height: auto;
}

#mute-btn:hover {
    scale: 1.1;
    transition: 0.2s;
    cursor: pointer;
}

#nightmode-btn, #mute-btn {
    z-index: 5;
}

.sidebar {
    z-index: 10;
}

.welcome {
    width: 300px;
    height: 100px;
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 1rem;
    left: 50%;
    top: 40%;
    transform: translate(-50%, -50%);
    padding: 0;
    margin: 0;
    box-shadow: 0 0 50px 20px rgba(255, 255, 255, 0.534);
}

#enter{
    border: none;
    padding: 0.25rem;
    transition: 0.1s;
}

#enter:hover{
    filter: invert(1);
    transition: 0.1s;
}

#enter:active{
    filter: invert(1);
    scale: 0.9;
    transition: 0.1s;
}

#icon-select-btn {
    border: none;
    background-color: transparent;
}

#icon-select-btn:hover {
    transition: 0.1s;
    scale: 1.1;
}

#icon-select-btn:active {
    transition: 0.1s;
    scale: 1.0;
}

.icon-select {
    width: 800px;
    height: 400px;
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 1rem;
    left: 50%;
    top: 40%;
    transform: translate(-50%, -50%);
    padding: 0;
    margin: 0;
    box-shadow: 0 0 50px 20px rgba(255, 255, 255, 0.534);
}

.icon-select img {
    width: 100px;
    height: auto;
}

.icon:hover {
    transition: 0.1s;
    scale: 1.1;
}

.icon:active {
    transition: 0.1s;
    scale: 1.0;
}

.currenticon {
    scale: 2;
}

h1, h2:hover {
    cursor: default;
}

button {
    cursor: pointer;
}

button:focus-visible {
    outline: none;
}

#delete-project {
    border: none;
    background-color: transparent;
    width: 30px;
    height: auto;
    display: none;
}

.delete-confirmation {
    width: 300px;
    height: 150px;
    position: absolute;
    display: flex;
    text-align: center;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 1rem;
    left: 50%;
    top: 40%;
    transform: translate(-50%, -50%);
    padding: 0;
    margin: 0;
    box-shadow: 0 0 50px 20px rgba(255, 255, 255, 0.534);
}

#confirm-buttons {
    display: flex;
    width: 50%;
    justify-content: center;
    gap: 1rem;
}

.delete-confirmation button {
    height: 1.5rem;
    border: none;
    padding: .5rem;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: 0.1s;
}

.delete-confirmation button:hover{
    filter: invert(1);
    transition: 0.1s;
}

.project-area-header {
    display: flex;
    justify-content: space-between;
    width: 100%;
}

.project-area-header-right {
    display: flex;
    gap: 1rem;
}

#sidebar-menu {
    display: none;
}

#sidebar-close {
    display: none;
}

@media screen and (max-width: 768px) {
    .header, .project-area {
        width: 90%;
        padding: 0.5rem;
        margin: 0;
    }

    .header {
        height: 10%;
        width: 100%;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        padding: 1rem;
        font-size: 20px;
    }

    .project-area {
        height: 90%;
    }

    .project-area-header {
        padding-right: 1rem;
    }

    .icons {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }

    .main {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
    }

    .project-area-header img {
        width: 30px;
    }

    .todo-item {
        max-width: 400px;
        max-height: 500px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        align-self: center;
        gap: 10px;
    }

    .sidebar {
        position: absolute;
        left: -100%;
        transition: left 0.3s;
        width: 100%;
        padding: 0.5rem;
        margin: 0;
    }

    .project-entry, .todo-entry {
        overflow: hidden;
    }

    .sidebar-header {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    #sidebar-menu {
        display: inline-flex;
        border: none;
        background-color: transparent;
        width: 50px;
        height: auto;
    }

    #sidebar-close {
        display: inline-flex;
        border: none;
        background-color: transparent;
        width: 50px;
        height: auto;
    }

    .sidebar.open {
        left: 0;
    }

    .project-area {
        grid-column: 1 / -1;
    }

    button {
        padding: 0.75rem;
        font-size: 1rem;
    }

    #delete-project {
        width: 50px;
        height: auto;
    }

    dialog {
        width: 90%;
        max-width: 350px;
    }

    .todo-list, .project-list {
        flex-direction: column;
    }
}


